import { create } from 'zustand'
import { persist } from 'zustand/middleware'
import { getAllPostsMeta, loadMarkdownPost } from '../utils/markdownLoader'

// 
const defaultBlogPosts = [
  {
    slug: 'responsive-design-guide',
    title: 'CSS',
    date: '2024-01-28',
    excerpt: '',
    content: '',
    category: '',
    tags: ['', 'CSS', '', '', ''],
    author: 'adventure',
    readingTime: '8'
  },
  {
    slug: 'javascript-async-guide',
    title: 'JavaScriptAsync/Await',
    date: '2024-01-22',
    excerpt: 'JavaScriptPromiseAsync/Await',
    content: 'JavaScriptPromiseAsync/AwaitJavaScript',
    category: '',
    tags: ['JavaScript', '', 'Promise', 'Async/Await', ''],
    author: 'adventure',
    readingTime: '10'
  },
  {
    slug: 'react-performance-optimization',
    title: 'React',
    date: '2024-01-15',
    excerpt: 'React',
    content: 'ReactReact',
    category: '',
    tags: ['React', '', '', '', ''],
    author: 'adventure',
    readingTime: '12'
  }
]

// 
const initialState = {
  posts: [], // 
  filteredPosts: [], // 
  categories: [], // 
  tags: [], // 
  selectedCategory: '', // 
  selectedTags: [], // 
  searchQuery: '', // 
  viewMode: 'grid', // 
  loading: false, // 
  currentPost: null, // 
  relatedPosts: [], // 
  isMarkdownLoaded: false // Markdown
}

//  store
export const useBlogStore = create(
  persist(
    (set, get) => ({
      ...initialState,

      // 
      initializePosts: async () => {
        console.log('...')
        set({ loading: true })
        
        try {
          // Markdown
          console.log('Markdown...')
          const markdownPosts = await getAllPostsMeta()
          
          console.log(`Markdown ${markdownPosts.length} `)
          
          if (markdownPosts.length > 0) {
            // Markdown
            console.log('Markdown')
            const categories = [...new Set(markdownPosts.map(post => post.category))]
            const allTags = [...new Set(markdownPosts.flatMap(post => post.tags || []))]
            
            set({ 
              posts: markdownPosts,
              filteredPosts: markdownPosts,
              categories,
              tags: allTags,
              isMarkdownLoaded: true
            })
            console.log('')
          } else {
            // Markdown
            console.log('Markdown')
            set({ 
              posts: defaultBlogPosts,
              filteredPosts: defaultBlogPosts,
              categories: ['', '', ''],
              tags: ['', 'CSS', 'JavaScript', 'React', 'Node.js', 'Python', ''],
              isMarkdownLoaded: false
            })
          }
        } catch (error) {
          console.error(':', error)
          // 
        }
        
        set({ loading: false })
      },

      // 
      setSearchQuery: (query) => {
        set({ searchQuery: query })
        get().filterPosts()
      },

      // 
      setSelectedCategory: (category) => {
        set({ selectedCategory: category })
        get().filterPosts()
      },

      // 
      setSelectedTags: (tags) => {
        set({ selectedTags: tags })
        get().filterPosts()
      },

      // 
      toggleViewMode: () => {
        set((state) => ({ viewMode: state.viewMode === 'grid' ? 'list' : 'grid' }))
      },

      // 
      filterPosts: () => {
        const { posts, searchQuery, selectedCategory, selectedTags } = get()
        
        let filtered = posts

        if (searchQuery) {
          filtered = filtered.filter(post => 
            post.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
            post.excerpt.toLowerCase().includes(searchQuery.toLowerCase()) ||
            post.content.toLowerCase().includes(searchQuery.toLowerCase()) ||
            post.tags.some(tag => tag.toLowerCase().includes(searchQuery.toLowerCase()))
          )
        }

        if (selectedCategory) {
          filtered = filtered.filter(post => post.category === selectedCategory)
        }

        if (selectedTags.length > 0) {
          filtered = filtered.filter(post => 
            selectedTags.some(tag => post.tags.includes(tag))
          )
        }

        set({ filteredPosts: filtered })
      },

      // slug
      getPostBySlug: async (slug) => {
        const { posts, isMarkdownLoaded } = get()
        
        // 
        let post = posts.find(p => p.slug === slug)
        
        // MarkdownMarkdown
        if (post && isMarkdownLoaded) {
          try {
            const markdownPost = await loadMarkdownPost(slug)
            if (markdownPost) {
              post = {
                ...post,
                content: markdownPost.content
              }
            }
          } catch (error) {
            console.error(`Markdown (${slug}):`, error)
          }
        }
        
        if (post) {
          const relatedPosts = posts
            .filter(p => p.category === post.category && p.slug !== slug)
            .slice(0, 3)
          
          set({ 
            currentPost: post,
            relatedPosts 
          })
        }
        
        return post
      },

      // 
      clearFilters: () => {
        set({
          searchQuery: '',
          selectedCategory: '',
          selectedTags: []
        })
        get().filterPosts()
      },

      // 
      getCategories: () => {
        return [...new Set(get().posts.map(post => post.category))]
      },

      // 
      getAllTags: () => {
        const allTags = get().posts.flatMap(post => post.tags)
        return [...new Set(allTags)]
      },

      // 
      getLatestPosts: (limit = 6) => {
        return get().posts
          .sort((a, b) => new Date(b.date) - new Date(a.date))
          .slice(0, limit)
      },

      // Home
      getTags: () => {
        const allTags = get().posts.flatMap(post => post.tags)
        return [...new Set(allTags)]
      }
    }),
    {
      name: 'blog-storage',
      partialize: (state) => ({
        viewMode: state.viewMode,
        selectedCategory: state.selectedCategory,
        selectedTags: state.selectedTags,
        searchQuery: state.searchQuery
      })
    }
  )
)