import matter from 'gray-matter'
import { Buffer } from 'buffer'

// Buffer polyfill
if (typeof window !== 'undefined' && !window.Buffer) {
  window.Buffer = Buffer
}

// Viteimport.meta.globsrc/postsMarkdown
const markdownFiles = import.meta.glob('/src/posts/*.md', { 
  query: '?raw',
  import: 'default',
  eager: false
})

// MarkdownGitHub Pages
function convertImagePaths(content) {
  // Markdown ![alt text](../../assets/images/image.png)
  const imageRegex = /!\[([^\]]*)\]\((\.\.\/\.\.\/assets\/images\/[^)]+)\)/g
  
  return content.replace(imageRegex, (match, altText, imagePath) => {
    // 
    const imageName = imagePath.split('/').pop()
    // GitHub Pages
    const basePath = '/page'
    return `![${altText}](${basePath}/assets/images/${imageName})`
  })
}

// Markdownfront matter
export async function loadMarkdownPost(slug) {
  try {
    const filePath = `/src/posts/${slug}.md`
    const loadFile = markdownFiles[filePath]
    
    if (!loadFile) {
      console.error(`Markdown: ${filePath}`)
      console.error(':', Object.keys(markdownFiles))
      return null
    }
    
    console.log(`: ${filePath}`)
    const markdownContent = await loadFile()
    console.log(`: ${markdownContent.length}`)
    
    // gray-matterfront matter
    const { data: frontMatter, content } = matter(markdownContent)
    console.log(`front matter:`, frontMatter)
    
    // GitHub Pages
    const processedContent = convertImagePaths(content)
    
    // 
    return {
      ...frontMatter,
      content: processedContent,
      slug
    }
  } catch (error) {
    console.error(`Markdown (${slug}):`, error)
    return null
  }
}

// slug
export async function getAllPostSlugs() {
  try {
    const slugs = Object.keys(markdownFiles).map(filePath => {
      // slug
      const match = filePath.match(/\/src\/posts\/(.*)\.md$/)
      return match ? match[1] : null
    }).filter(Boolean)
    
    return slugs
  } catch (error) {
    console.error(':', error)
    return []
  }
}

// 
export async function getAllPostsMeta() {
  try {
    console.log('...')
    console.log('markdown:', Object.keys(markdownFiles))
    
    const slugs = await getAllPostSlugs()
    console.log('slugs:', slugs)
    
    const posts = []
    
    for (const slug of slugs) {
      console.log(`: ${slug}`)
      const post = await loadMarkdownPost(slug)
      if (post) {
        console.log(`: ${post.title}`)
        posts.push({
          slug: post.slug,
          title: post.title,
          date: post.date,
          excerpt: post.excerpt,
          category: post.category,
          tags: post.tags || [],
          author: post.author,
          readingTime: post.readingTime
        })
      } else {
        console.log(`: ${slug}`)
      }
    }
    
    // 
    posts.sort((a, b) => new Date(b.date) - new Date(a.date))
    
    console.log(` ${posts.length} `)
    return posts
  } catch (error) {
    console.error(':', error)
    return []
  }
}